.card {
    background-color: #FAFFD6;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
  }
  body {
    background-color: #B0E1A2;
  }
  .container {
    background-color: #CBF5AF;
    border-radius: 15px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
  }

  .form-control{
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
border-radius: 25px ;
  }

  .hello{
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
    padding: 0px 15px 30px 15px;
    border-radius: 15px;
  }

  .link{
    text-decoration: none;
    border: 1px solid transparent;
    background-color: #FFA1B3;
    padding:  2px 6px 3px 6px;
    border-radius: 15px ;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
    transition: background-color 0.1s ease-in-out;
    color: darkgreen;
  }
  .link:hover{
    background-color: #FAFFD6;
  }

  .sign{
     background-color: #CBF5AF;
     border: 1px solid transparent;
     padding: 10px;
    border-radius: 15px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);
  }

  .btn{
    border-radius: 25px;
    padding: 2px 15px;
    box-shadow: 0px 5px 10px rgba(0, 0, 0, 0.2), 0px 10px 20px rgba(0, 0, 0, 0.2), 0px 15px 30px rgba(0, 0, 0, 0.2);

  }




